export default {
	props: {
		item: Object,
		index: Number
	},
	data(){
		 return{
			isFollow:this.item.isFollow,
			infoNum:this.item.infoNum
		 }
	},
	methods: {
		guanzhu(){
			this.isFollow=true;
			uni.showToast({
				title:'关注成功'
			})
		},
		caozuo(type) {
			switch (type) {
				case "ding":
					if (this.infoNum.index == 1) {
						return;
					}
					this.infoNum.agreeNum++;
					if (this.infoNum.index == 2) {
						this.infoNum.disAgreeNum--;
					}
					this.infoNum.index = 1;

					break;
				case "cai":
					if (this.infoNum.index == 2) {
						return;
					}
					this.infoNum.disAgreeNum++;
					if (this.infoNum.index == 1) {
						this.infoNum.agreeNum--;
					}
					this.infoNum.index = 2;
					break;
			}
		}
	}

}
